.home {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.media-home {
    margin: 0.5rem;
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.media-home i {
    font-size: 3rem;
    color: #2494c1;
    margin: 10px;
}

.fab .fas {
    font-family: "Font Awesome 5 Brands";
}

h3 {
    font-family: 'Nunito', sans-serif;
    padding: .5rem;
    text-align: center;
    font-style: italic;
    margin-bottom: 20px;
}

p {
    padding: .5rem;
    margin-bottom: 25px;
} 

.card-tech {
    padding: 0;
    margin: 0;
}

.tech {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 25px;
}

.tech li{
    font-size: 3rem;
    padding: 1.5rem;
}

/* icons set color section */
.fa-html5 {
    color: #E34F26;
}
.fa-css3-alt {
    color: #016DB5;
}
.fa-js-square {
    color: #FDDA3E;
}
.fa-react {
    color: #5ED4F4;
}
.fa-node-js {
    color: #8CC13F;
}
.fa-database {
    color: #13A54F;
}

@media (min-width: 768px) {

    
    .home {
        flex-direction: row;
        justify-content: center;
        margin-top: 8rem;
    }

    .davide-home {
        width: 45rem;
       
    }

    .davide-home h3 {
       text-align: left;
    }

    .media-home {
        justify-content: left;
    }
    
    
}